import { sidebarContext } from "../../App";
import { Db } from "../../database/Db_new";
import "dexie-export-import";

import download from "downloadjs";
import React, {
	Suspense,
	useState,
	useContext,
	useImperativeHandle,
	useRef,
	forwardRef,
} from "react";
import styles from "./Sidebar.module.scss";
import Header from "./Header";
import { toast } from "react-toastify";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
	faClosedCaptioning,
	faFileExport,
	faFileImport,
	faMagnifyingGlass,
	faNotesMedical,
	faTags,
} from "@fortawesome/free-solid-svg-icons";
import SidebarTags from "./Sidebar_Tags";
const HistoryDisplay = React.lazy(() => import("./Sidebar_History"));
const SidebarNote = React.lazy(() => import("./Sidebar_Note"));
// const SidebarTags = React.lazy(() => import("./Sidebar_Tags"));

const handleImportDb = async (event) => {
	let file = event.target.files[0];
	//let label = event.target.nextElementSibling;
	try {
		if (!file) {
			toast.error("Only accept JSON files", {
				position: toast.POSITION.BOTTOM_CENTER,
			});
			throw new Error(`Only accept JSON files`);
		}
		console.log("Importing " + file.name);
		await Db.open();
		await Db.import(file, {
			progressCallback: ({ totalRows, completedRows }) => {
				console.log(
					`Progress: ${completedRows} of ${totalRows} rows completed`
				);
			},
		});
		//label.innerHTML = file.name.replace(".JSON", "");
		toast.success("Import completed!", {
			position: toast.POSITION.BOTTOM_CENTER,
		});
	} catch (error) {
		console.error("" + error);
	}
};
function Sidebar(props, ref) {
	const [initialPos, setInitialPos] = useState(null);
	const [initialSize, setInitialSize] = useState(null);
	const [tab, setTab] = useState("");
	const HistoryRef = useRef();
	function progressCallback({ totalRows, completedRows, done }) {
		console.log(
			`Progress: ${completedRows} of ${totalRows} rows completed`
		);
	}
	const tabs_lable = [
		{ lable: "search", icon: <FontAwesomeIcon icon={faMagnifyingGlass} /> },
		{ lable: "note", icon: <FontAwesomeIcon icon={faNotesMedical} /> },
		{ lable: "tags", icon: <FontAwesomeIcon icon={faTags} /> },
		// { label: "pinyin", icon:<FontAwesomeIcon icon={faClosedCaptioning}/>},
	];
	let v = useContext(sidebarContext);
	const handleTabOnchange = (_tab) => {
		if (_tab === "search") v.onSetScreen("viewr_char");
		if (_tab === "note") v.onSetScreen("note");
		if (_tab === "tags") v.onSetScreen("tags");
		// if(_tab === "pinyin")v.onSetScreen("view_pinyin");
		setTab(_tab);
	};

	const initial = (e) => {
		const resizable = document.getElementById("sidebar");
		setInitialPos(e.clientX);
		setInitialSize(resizable.offsetWidth);
	};
	const resize = (e) => {
		const resizable = document.getElementById("sidebar");

		let width = parseInt(initialSize) + parseInt(e.clientX - initialPos);
		resizable.style.width = `${width > 320 ? 320 : width}px`;
	};

	useImperativeHandle(ref, () => {
		return {
			saveHistory: function (v) {
				HistoryRef.current.saveHistory(v);
			},
		};
	});

	return (
		<div
			className={styles.wrapper}
			id='sidebar'
			draggable='true'
			onDragStart={initial}
			onDrag={resize}
		>
			<div className={styles["sidebar_body"]}>
				<Header>
					{tabs_lable.map((currentTab, index) => {
						let tabActiveStyle =
							currentTab.lable === tab ? styles.tabActive : null;
						return (
							<button
								className={`${styles.tab_btnStyle} ${tabActiveStyle}`}
								onClick={() => {
									handleTabOnchange(currentTab.lable);
								}}
								key={index}
								tabIndex={4 + index}
							>
								{currentTab.icon}
							</button>
						);
					})}
					<div className={styles["export-wrapper"]}>
						<input
							title='import your data'
							type={"file"}
							name='file'
							id='file'
							className={styles["inputfile"]}
							onChange={handleImportDb}
						/>
						<label htmlFor='file' title='import database'>
							<FontAwesomeIcon icon={faFileImport} />
						</label>
						<button
							title='export database'
							onClick={async () => {
								try {
									const blob = await Db.export({
										prettyJson: true,
										progressCallback,
									}).then(() => {
										toast.info(
											"file is ready to download",
											{
												position:
													toast.POSITION.BOTTOM_RIGHT,
											}
										);
									});
									download(
										blob,
										"My-dexie-export.json",
										"application/json"
									);
								} catch (error) {
									console.error("" + error);
								}
							}}
						>
							<FontAwesomeIcon icon={faFileExport} />
						</button>
					</div>
					{props.children}
				</Header>
				<Suspense fallback={<div>Loading...</div>}>
					{tab === "search" && <HistoryDisplay ref={HistoryRef} />}
					{tab === "note" && <SidebarNote />}
					{tab === "tags" && <SidebarTags />}
				</Suspense>
			</div>
		</div>
	);
}

export default forwardRef(Sidebar);
